.chef-img {
    background-image: url('../../../assets/chef.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height: 85vh;
    position: relative;
}

.chef-content {
    position: absolute;
    bottom: 5%;
    padding: 10px;
    background-color: var(--color-primary);
}

.chef-info {
    display: flex;
    align-items: center;
    gap: 10px;
}

.chef-info h4 {
    text-transform: uppercase;
}

.chef-info p {
    padding-top: 10px;
}